@font-face {
    font-family: 'Realist';
    src: url('realist_semilight-webfont.woff2') format('woff2'),
    url('realist_semilight-webfont.woff') format('woff'),
    url('realist_semilight-webfont.svg#realistsemilight') format('svg');
    font-weight: 100;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'Realist';
    src: url('realist_semilight_italic-webfont.woff2') format('woff2'),
    url('realist_semilight_italic-webfont.woff') format('woff'),
    url('realist_semilight_italic-webfont.svg#realistsemilight_italic') format('svg');
    font-weight: 100;
    font-style: italic;
    font-display: swap;
}

